//  src/pages/Inscription.jsx
import React, { useEffect, useState } from "react";
import Section from "@/components/ui/Section";
import toast from "react-hot-toast";
import api from "@/utils/api";

const MODULES = [
  "Anglais Gnral",
  "Anglais Professionnel",
  "Chinois (Mandarin)",
  "Espagnol",
  "Franais (FLE)",
  "Prparation TOEFL/IELTS",
];

const TRANCHES = [
  "Matin (08:0010:00)",
  "Matin (10:0012:00)",
  "Aprs-midi (14:0016:00)",
  "Aprs-midi (16:0018:00)",
  "Soir (18:0020:00)",
  "Week-end",
];

const NIVEAUX = [
  "Dbutant (A1)",
  "lmentaire (A2)",
  "Intermdiaire (B1)",
  "Intermdiaire+ (B2)",
  "Avanc (C1/C2)",
];

export default function Inscription() {
  const [form, setForm] = useState({
    sessionStart: "",
    sessionEnd: "",
    nom: "",
    prenom: "",
    sexe: "",
    dateNaissance: "",
    lieuNaissance: "",
    moduleFormation: "",
    trancheHeure: "",
    niveau: "",
    groupe: "",
    quartier: "",
    arrondissement: "",
    telephone: "",
    whatsapp: "",
    email: "",
    acceptFees: false,
    whatsappSameAsTel: true,
  });

  // Si coch, garder WhatsApp synchronis avec tlphone
  useEffect(() => {
    if (form.whatsappSameAsTel) {
      setForm((f) => ({ ...f, whatsapp: f.telephone }));
    }
  }, [form.telephone, form.whatsappSameAsTel]);

  const onChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((f) => ({ ...f, [name]: type === "checkbox" ? checked : value }));
  };

  const validate = () => {
    const required = [
      "sessionStart",
      "sessionEnd",
      "nom",
      "prenom",
      "sexe",
      "dateNaissance",
      "lieuNaissance",
      "moduleFormation",
      "trancheHeure",
      "niveau",
      "quartier",
      "arrondissement",
      "telephone",
      "email",
    ];
    for (const k of required) {
      if (!String(form[k]).trim()) {
        toast.error("Merci de remplir tous les champs obligatoires.");
        return false;
      }
    }
    if (!form.acceptFees) {
      toast.error(
        "Veuillez accepter que les frais d'inscription sont obligatoires et non remboursables."
      );
      return false;
    }
    return true;
  };

  const [submitting, setSubmitting] = useState(false);

  const onSubmit = async (e) => {
    e.preventDefault();
    if (!validate()) return;
    try {
      setSubmitting(true);
      // Mapping ct API
      const payload = {
        session_start: form.sessionStart,
        session_end: form.sessionEnd,
        nom: form.nom,
        prenom: form.prenom,
        sexe: form.sexe,
        date_naissance: form.dateNaissance,
        lieu_naissance: form.lieuNaissance,
        module_formation: form.moduleFormation,
        tranche_heure: form.trancheHeure,
        niveau: form.niveau,
        groupe: form.groupe || null,
        quartier: form.quartier,
        arrondissement: form.arrondissement,
        telephone: form.telephone,
        whatsapp: form.whatsapp,
        email: form.email,
        accept_non_remboursable: form.acceptFees,
        source: "web-form",
      };

      await api.post("/inscription", payload);
      toast.success("Inscription envoye avec succs !");
      setForm({
        sessionStart: "",
        sessionEnd: "",
        nom: "",
        prenom: "",
        sexe: "",
        dateNaissance: "",
        lieuNaissance: "",
        moduleFormation: "",
        trancheHeure: "",
        niveau: "",
        groupe: "",
        quartier: "",
        arrondissement: "",
        telephone: "",
        whatsapp: "",
        email: "",
        acceptFees: false,
        whatsappSameAsTel: true,
      });
    } catch (e) {
      console.error(e);
      const msg = e?.response?.data?.message || "Erreur lors de l'envoi.";
      toast.error(msg);
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Section
      id="inscription"
      title="Fiche d'inscription"
      subtitle="Merci de complter les informations suivantes. (Registration sheet)"
    >
      <form onSubmit={onSubmit} className="grid grid-cols-1 gap-6">
        {/* SESSION */}
        <fieldset className="bg-white rounded-2xl shadow p-6">
          <legend className="font-semibold text-brand">Session / Cohorte</legend>
          <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium">Du (From)</label>
              <input type="date" name="sessionStart" value={form.sessionStart} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium">Au (To)</label>
              <input type="date" name="sessionEnd" value={form.sessionEnd} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
          </div>
        </fieldset>

        {/* IDENTIT */}
        <fieldset className="bg-white rounded-2xl shadow p-6">
          + <legend className="font-semibold text-brand">Identit</legend>
          <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium">Nom (Last name)</label>
              <input name="nom" value={form.nom} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium">Prnom (First name)</label>
              <input name="prenom" value={form.prenom} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium">Sexe (Sex)</label>
              <select name="sexe" value={form.sexe} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required>
                <option value=""></option>
                <option value="F">Fminin</option>
                <option value="M">Masculin</option>
                <option value="Autre">Autre</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium">Date de naissance (DOB)</label>
              <input type="date" name="dateNaissance" value={form.dateNaissance} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div className="md:col-span-2">
              <label className="block text-sm font-medium">Lieu de naissance (Place of birth)</label>
              <input name="lieuNaissance" value={form.lieuNaissance} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
          </div>
        </fieldset>

        {/* FORMATION */}
        <fieldset className="bg-white rounded-2xl shadow p-6">
          <legend className="font-semibold text-brand">Formation</legend>
          <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium">Module de formation (Training module)</label>
              <select name="moduleFormation" value={form.moduleFormation} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required>
                <option value=""></option>
                {MODULES.map((m) => (
                  <option key={m} value={m}>{m}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium">Tranche d'heure (Time slot)</label>
              <select name="trancheHeure" value={form.trancheHeure} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required>
                <option value=""></option>
                {TRANCHES.map((t) => (
                  <option key={t} value={t}>{t}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium">Niveau (Level)</label>
              <select name="niveau" value={form.niveau} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required>
                <option value=""></option>
                {NIVEAUX.map((n) => (
                  <option key={n} value={n}>{n}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium">Groupe (Group)</label>
              <input name="groupe" value={form.groupe} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" placeholder="ex: A1-3" />
            </div>
          </div>
        </fieldset>

        {/* COORDONNES */}
        <fieldset className="bg-white rounded-2xl shadow p-6">
          <legend className="font-semibold text-brand">Coordonnes</legend>
          <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium">Quartier (Area)</label>
              <input name="quartier" value={form.quartier} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium">Arrondissement (District)</label>
              <input name="arrondissement" value={form.arrondissement} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium">Tlphone</label>
              <input name="telephone" value={form.telephone} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
            <div>
              <label className="block text-sm font-medium flex items-center gap-2">WhatsApp
                <label className="text-xs font-normal inline-flex items-center gap-2 ml-2">
                  <input type="checkbox" name="whatsappSameAsTel" checked={form.whatsappSameAsTel} onChange={onChange} />
                  identique au tlphone
                </label>
              </label>
              <input name="whatsapp" value={form.whatsapp} onChange={onChange} disabled={form.whatsappSameAsTel} className="mt-1 w-full border rounded px-3 py-2 disabled:bg-gray-50" />
            </div>
            <div className="md:col-span-2">
              <label className="block text-sm font-medium">Email</label>
              <input type="email" name="email" value={form.email} onChange={onChange} className="mt-1 w-full border rounded px-3 py-2" required />
            </div>
          </div>
        </fieldset>

        {/* ACCEPTATION */}
        <div className="bg-white rounded-2xl shadow p-6">
          <label className="inline-flex items-start gap-3">
            <input type="checkbox" name="acceptFees" checked={form.acceptFees} onChange={onChange} />
            <span className="text-sm text-gray-700">
              Jai lu et jaccepte que <strong>les frais dinscription sont obligatoires et non remboursables</strong> (mme en cas de dsistement).
            </span>
          </label>
        </div>

        <div className="flex items-center justify-end gap-3">
          <button type="reset" className="px-5 py-2 rounded border hover:bg-gray-50"
            onClick={() => setForm((f) => ({
              sessionStart: "", sessionEnd: "", nom: "", prenom: "", sexe: "",
              dateNaissance: "", lieuNaissance: "", moduleFormation: "",
              trancheHeure: "", niveau: "", groupe: "", quartier: "",
              arrondissement: "", telephone: "", whatsapp: "", email: "",
              acceptFees: false, whatsappSameAsTel: true,
            }))}
          >
            Effacer
          </button>
          <button type="submit" disabled={submitting} className="px-6 py-2 rounded bg-[#1F75BB] text-white font-semibold hover:bg-[#155b94] disabled:opacity-60">
            {submitting ? "Envoi" : "Envoyer linscription"}
          </button>
        </div>
      </form>
    </Section>
  );
}
