//  src/pages/auth/Register.jsx
import { useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";

const USERNAME_RE = /^[a-z0-9._-]{3,20}$/;

export default function Register() {
  const { register } = useAuth();
  const nav = useNavigate();

  const [form, setForm] = useState({
    full_name: "",
    email: "",
    username: "",
    password: "",
  });
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");

  async function onSubmit(e) {
    e.preventDefault();
    setErr(""); setLoading(true);

    const payload = {
      full_name: form.full_name.trim(),
      email: form.email.trim(),
      username: form.username.trim().toLowerCase(),
      password: form.password,
    };

    if (!USERNAME_RE.test(payload.username)) {
      setErr("Identifiant invalide (320 caractres: az, 09, . _ -)");
      setLoading(false);
      return;
    }

    try {
      await register(payload);
      nav("/dashboard", { replace: true });
    } catch (e2) {
      setErr(e2?.response?.data?.error || "Inscription impossible");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="min-h-[80vh] grid place-items-center p-4">
      <form onSubmit={onSubmit} className="w-full max-w-md bg-white rounded-2xl shadow p-6 space-y-4">
        <h1 className="text-2xl font-bold text-center">Crer un compte</h1>
        {err && <div className="text-red-600 text-sm">{err}</div>}

        <input
          className="w-full border rounded-xl p-3"
          placeholder="Nom complet"
          value={form.full_name}
          onChange={(e)=>setForm({...form, full_name:e.target.value})}
          required
        />
        <input
          className="w-full border rounded-xl p-3"
          type="email"
          placeholder="Email"
          value={form.email}
          onChange={(e)=>setForm({...form, email:e.target.value})}
          required
        />
        <input
          className="w-full border rounded-xl p-3"
          placeholder="Identifiant (prenom12)"
          value={form.username}
          onChange={(e)=>setForm({...form, username:e.target.value.toLowerCase()})}
          pattern="[a-z0-9._\-]{3,20}"  //  tiret chapp
          title="3-20 caractres: lettres/chiffres, . _ -"
          required
        />
        <input
          className="w-full border rounded-xl p-3"
          type="password"
          placeholder="Mot de passe"
          value={form.password}
          onChange={(e)=>setForm({...form, password:e.target.value})}
          required
        />

        <button disabled={loading} className="w-full btn-brand">
          {loading ? "Cration" : "Crer mon compte"}
        </button>

        <p className="text-center text-sm">
          Dj inscrit ? <Link to="/account?tab=login" className="link-brand">Se connecter</Link>
        </p>
      </form>
    </div>
  );
}
