//  src/routes/PublicRoutes.jsx
import React from "react";
import MainLayout from "@/layouts/MainLayout";

// Pages publiques
import Home from "@/pages/Home";
import About from "@/pages/About";
import Inscription from "@/pages/Inscription";
import Contact from "@/pages/public/Contact";
import Courses from "@/pages/public/Formations"; // /courses -> Formations.jsx
import Equipe from "@/pages/Equipe";
import Bourses from "@/pages/Bourses";
import Galerie from "@/pages/public/Galerie";
import NotFound from "@/pages/NotFound";

// Auth
import Login from "@/pages/auth/Login";
import Register from "@/pages/auth/Register";

// Protg (tudiant)
import ProtectedRoute from "@/routes/ProtectedRoute";
import Dashboard from "@/pages/Dashboard";
import Profile from "@/pages/Profile";
import MyCourses from "@/pages/MyCourses";
import Assignments from "@/pages/Assignments";
import AssignmentDetail from "@/pages/AssignmentDetail";
import CourseDetailTimeline from "@/pages/CourseDetailTimeline";
import Account from "@/pages/auth/Account";

export const publicRoutes = {
  path: "/",
  element: <MainLayout />,
  children: [
    // ---- Public
    { index: true, element: <Home /> },
    { path: "about", element: <About /> },
    { path: "equipe", element: <Equipe /> },
    { path: "inscription", element: <Inscription /> },
    { path: "bourses", element: <Bourses /> },
    { path: "galerie", element: <Galerie /> },
    { path: "contact", element: <Contact /> },
    { path: "courses", element: <Courses /> },
    { path: "account", element: <Account /> }, // page ddie (onglets)
    // { path: "formations", element: <Courses /> }, // si tu veux aussi l'URL FR

    // ---- Auth (publiques)
    { path: "login", element: <Login /> },
    { path: "register", element: <Register /> },

    // ---- Espace tudiant (protg)
    {
      element: <ProtectedRoute />,
      children: [
        { path: "dashboard", element: <Dashboard /> },
        { path: "profile", element: <Profile /> },
        { path: "my-courses", element: <MyCourses /> },
        { path: "assignments", element: <Assignments /> },
        { path: "assignments/:id", element: <AssignmentDetail /> },
        { path: "courses/:slug", element: <CourseDetailTimeline /> }, // timeline/cartes anime
      ],
    },

    // ---- 404
    { path: "*", element: <NotFound /> },
  ],
};
