//  src/pages/Dashboard.jsx
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import api from "@/utils/api";
import { asArray } from "@/utils/asArray";

export default function Dashboard() {
  const nav = useNavigate();

  // null = loading; [] = vide
  const [enrollments, setEnrollments] = useState(null);
  const [assignments, setAssignments] = useState(null);
  const [err, setErr] = useState("");

  useEffect(() => {
    let alive = true;
    (async () => {
      try {
        const [enrRes, asgRes] = await Promise.allSettled([
          api.get("/enrollments/me"),
          api.get("/assignments"),
        ]);

        if (!alive) return;

        if (enrRes.status === "fulfilled") {
          setEnrollments(asArray(enrRes.value.data));
        } else {
          setEnrollments([]);
          setErr((e) => e || "Impossible de charger vos cours");
        }

        if (asgRes.status === "fulfilled") {
          setAssignments(asArray(asgRes.value.data));
        } else {
          setAssignments([]);
          setErr((e) => e || "Impossible de charger les devoirs");
        }
      } catch (e) {
        if (!alive) return;
        setEnrollments([]);
        setAssignments([]);
        setErr("Erreur rseau");
      }
    })();
    return () => { alive = false; };
  }, []);

  // Loading state
  if (enrollments === null || assignments === null) {
    return <div className="p-4">Chargement</div>;
  }

  const hasCourses = asArray(enrollments).length > 0;
  const hasAssignments = asArray(assignments).length > 0;

  return (
    <div className="max-w-6xl mx-auto p-4 space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Tableau de bord</h1>
        <div className="flex items-center gap-2">
          <Link to="/my-courses" className="btn-outline-brand">Mes cours</Link>
          <Link to="/assignments" className="btn-brand">Mes devoirs</Link>
        </div>
      </div>

      {err && <div className="text-red-600 text-sm">{err}</div>}

      {/* Aperu des cours */}
      <section className="space-y-3">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold">Mes cours</h2>
          {hasCourses && <Link to="/my-courses" className="link-brand">Tout voir</Link>}
        </div>

        {!hasCourses ? (
          <div className="rounded-2xl bg-white p-5 border text-sm text-gray-600">
            Vous ntes inscrit  aucun cours. <Link to="/courses" className="link-brand">Parcourir les formations</Link>.
          </div>
        ) : (
          <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {asArray(enrollments).slice(0, 6).map((e) => (
              <div key={e.id} className="rounded-2xl bg-white border p-4">
                <div className="text-sm text-gray-500">{e.category} {e.level ? ` ${e.level}` : ""}</div>
                <h3 className="card-title">{e.title}</h3>
                <div className="mt-2 h-2 rounded bg-gray-100 overflow-hidden">
                  <div
                    className="h-full bg-brand"
                    style={{ width: `${Math.max(0, Math.min(100, e.progress_percent || 0))}%` }}
                  />
                </div>
                <div className="mt-1 text-xs text-gray-600">
                  Progression : {Math.round(Math.max(0, Math.min(100, e.progress_percent || 0)))}%
                </div>
                <button
                  onClick={() => nav(`/courses/${e.slug}`)}
                  className="mt-3 btn-brand w-full"
                >
                  Ouvrir
                </button>
              </div>
            ))}
          </div>
        )}
      </section>

      {/* Aperu des devoirs */}
      <section className="space-y-3">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold">Mes devoirs</h2>
          {hasAssignments && <Link to="/assignments" className="link-brand">Tout voir</Link>}
        </div>

        {!hasAssignments ? (
          <div className="rounded-2xl bg-white p-5 border text-sm text-gray-600">
            Aucun devoir pour linstant.
          </div>
        ) : (
          <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {asArray(assignments).slice(0, 6).map((a) => (
              <div key={a.id} className="rounded-2xl bg-white border p-4 space-y-2">
                <h3 className="card-title">{a.title}</h3>
                <p className="text-sm text-gray-600">
                  {a.instructions ? String(a.instructions).slice(0, 120) : ""}
                </p>
                <div className="text-sm">
                  chance : {a.due_at ? new Date(a.due_at).toLocaleString() : ""}
                </div>
                <Link to={`/assignments/${a.id}`} className="btn-outline-brand w-full text-center">
                  Ouvrir
                </Link>
              </div>
            ))}
          </div>
        )}
      </section>
    </div>
  );
}
