import { useState } from "react";
import { useNavigate, useLocation, Link } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";

export default function Login() {
  const { login } = useAuth();
  const nav = useNavigate();
  const { state } = useLocation();
  const [form, setForm] = useState({ email: "", password: "" });
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");

  async function onSubmit(e) {
    e.preventDefault();
    setErr(""); setLoading(true);
    try {
      await login(form.email.trim(), form.password);
      nav(state?.from || "/dashboard", { replace: true });
    } catch (e2) {
      setErr(e2?.response?.data?.error || "Impossible de se connecter");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="min-h-[80vh] grid place-items-center p-4">
      <form onSubmit={onSubmit} className="w-full max-w-md bg-white rounded-2xl shadow p-6 space-y-4">
        <h1 className="text-2xl font-bold text-center">Connexion</h1>
        {err && <div className="text-red-600 text-sm">{err}</div>}
        <input className="w-full rounded-xl border p-3" type="email" placeholder="Email" value={form.email}
               onChange={(e)=>setForm({...form, email:e.target.value})} required />
        <input className="w-full rounded-xl border p-3" type="password" placeholder="Mot de passe" value={form.password}
               onChange={(e)=>setForm({...form, password:e.target.value})} required />
        <button disabled={loading} className="w-full rounded-xl bg-blue-600 text-white py-3">
          {loading ? "Connexion" : "Se connecter"}
        </button>
        <p className="text-center text-sm">
          Pas de compte ? <Link to="/register" className="text-blue-600">Crer un compte</Link>
        </p>
      </form>
    </div>
  );
}
