//  src/utils/api.js
import axios from "axios";

const api = axios.create({
  baseURL: import.meta.env.VITE_API_BASE_URL ?? "/api",
  withCredentials: false,        // JWT en Authorization: Bearer
  timeout: 15000,                // 15s
});

//  Request: injecte le token
api.interceptors.request.use((config) => {
  const token = localStorage.getItem("hc_token");
  if (token) config.headers.Authorization = `Bearer ${token}`;
  return config;
});

//  Response: auto-logout soft sur 401/403 (optionnel)
api.interceptors.response.use(
  (res) => res,
  (err) => {
    const status = err?.response?.status;
    if (status === 401 || status === 403) {
      localStorage.removeItem("hc_token");
      // redirige vers la page compte si on n'y est pas dj
      if (!location.pathname.startsWith("/account")) {
        location.assign("/account?tab=login");
      }
    }
    return Promise.reject(err);
  }
);

export default api;
