//  src/pages/auth/Account.jsx
import { useEffect, useMemo, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";

const USERNAME_RE = /^[a-z0-9._-]{3,20}$/; // validation JS de secours

export default function Account() {
  const { login, register } = useAuth();
  const nav = useNavigate();
  const [sp] = useSearchParams();

  // ?tab=register pour ouvrir directement l'onglet inscription
  const initialTab = useMemo(
    () => (sp.get("tab") === "register" ? "register" : "login"),
    [sp]
  );

  const [tab, setTab] = useState(initialTab);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");

  useEffect(() => setTab(initialTab), [initialTab]);

  async function submitLogin(e) {
    e.preventDefault();
    setErr(""); setLoading(true);
    const fd = new FormData(e.currentTarget);
    try {
      await login(fd.get("identifier"), fd.get("password"));
      nav("/dashboard", { replace: true });
    } catch (e2) {
      setErr(e2?.response?.data?.error || "Impossible de se connecter");
    } finally {
      setLoading(false);
    }
  }

  async function submitRegister(e) {
    e.preventDefault();
    setErr(""); setLoading(true);
    const fd = new FormData(e.currentTarget);

    const full_name = String(fd.get("full_name") || "").trim();
    const email = String(fd.get("email") || "").trim();
    const username = String(fd.get("username") || "").trim().toLowerCase();
    const password = fd.get("password");

    // garde-fou ct JS
    if (!USERNAME_RE.test(username)) {
      setErr("Identifiant invalide (320 caractres: az, 09, . _ -)");
      setLoading(false);
      return;
    }

    try {
      await register({ full_name, email, username, password });
      nav("/dashboard", { replace: true });
    } catch (e2) {
      setErr(e2?.response?.data?.error || "Inscription impossible");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="min-h-[80vh] grid place-items-center p-4">
      <div className="w-full max-w-md bg-white rounded-2xl shadow p-6">
        {/* Onglets */}
        <div className="flex items-center gap-2 mb-4">
          <button
            onClick={() => setTab("login")}
            className={`px-3 py-2 rounded-xl font-semibold text-sm ${
              tab === "login" ? "btn-brand text-white" : "btn-outline-brand"
            }`}
          >
            Connexion
          </button>
          <button
            onClick={() => setTab("register")}
            className={`px-3 py-2 rounded-xl font-semibold text-sm ${
              tab === "register" ? "btn-brand text-white" : "btn-outline-brand"
            }`}
          >
            Crer un compte
          </button>
        </div>

        {err && <div className="mb-3 text-red-600 text-sm">{err}</div>}

        {/* Formulaires */}
        {tab === "login" ? (
          <form onSubmit={submitLogin} className="space-y-3">
            <input
              name="identifier"
              className="w-full border rounded-xl p-3"
              placeholder="Email ou identifiant"
              required
            />
            <input
              name="password"
              type="password"
              className="w-full border rounded-xl p-3"
              placeholder="Mot de passe"
              required
            />
            <button disabled={loading} className="w-full btn-brand">
              {loading ? "Connexion" : "Se connecter"}
            </button>
            <p className="text-sm text-center mt-1">
              Nouveau ?{" "}
              <button
                type="button"
                className="link-brand"
                onClick={() => setTab("register")}
              >
                Crer un compte
              </button>
            </p>
          </form>
        ) : (
          <form onSubmit={submitRegister} className="space-y-3">
            <input
              name="full_name"
              className="w-full border rounded-xl p-3"
              placeholder="Nom complet"
              required
            />
            <input
              name="email"
              type="email"
              className="w-full border rounded-xl p-3"
              placeholder="Email"
              required
            />
            <input
              name="username"
              className="w-full border rounded-xl p-3"
              placeholder="Identifiant (prenom12)"
              pattern="[a-z0-9._\-]{3,20}"  //  tiret chapp pour Firefox
              title="3-20 caractres: lettres/chiffres, . _ -"
              onInput={(e) => (e.target.value = e.target.value.toLowerCase())}
              required
            />
            <input
              name="password"
              type="password"
              className="w-full border rounded-xl p-3"
              placeholder="Mot de passe"
              required
            />
            <button disabled={loading} className="w-full btn-brand">
              {loading ? "Cration" : "Crer mon compte"}
            </button>
            <p className="text-sm text-center mt-1">
              Dj inscrit ?{" "}
              <button
                type="button"
                className="link-brand"
                onClick={() => setTab("login")}
              >
                Se connecter
              </button>
            </p>
          </form>
        )}
      </div>
    </div>
  );
}
