//  src/App.jsx
import { BrowserRouter } from "react-router-dom";
import { useRoutes } from "react-router-dom";
import { publicRoutes } from "@/routes/PublicRoutes";
import { adminRoutes } from "@/routes/AdminRoutes";
import { AuthProvider } from "@/context/AuthContext";

// Normalise pour que useRoutes reoive toujours un tableau de route objects
function normalizeRoutes(items) {
  return items
    .filter(Boolean) // enlve undefined / null
    .flatMap((r) => (Array.isArray(r) ? r : [r])); // accepte objet OU tableau
}

function AppRoutes() {
  const routes = normalizeRoutes([publicRoutes, adminRoutes]);
  return useRoutes(routes);
}

export default function App() {
  return (
    <AuthProvider>
      <BrowserRouter>
        <AppRoutes />
      </BrowserRouter>
    </AuthProvider>
  );
}
