//  src/layouts/AdminLayout.jsx
import { NavLink, Outlet, useNavigate } from "react-router-dom";
import { LogOut, LayoutDashboard } from "lucide-react";
import { useMemo } from "react";

export default function AdminLayout() {
  const navigate = useNavigate();
  const role = useMemo(() => localStorage.getItem("adminRole") || sessionStorage.getItem("adminRole"), []);

  const logout = () => {
    localStorage.removeItem("adminToken");
    localStorage.removeItem("adminRole");
    sessionStorage.removeItem("adminToken");
    sessionStorage.removeItem("adminRole");
    navigate("/admin/login", { replace: true });
  };

  return (
    <div className="min-h-screen grid grid-cols-12 bg-gray-50">
      {/* Sidebar */}
      <aside className="col-span-12 md:col-span-3 lg:col-span-2 bg-white border-r">
        <div className="p-4 border-b">
          <div className="flex items-center gap-2">
            <img src="/images/logo-harvest.jpg" alt="Harvest Center" className="h-8" />
            <span className="font-semibold text-brand">Admin</span>
          </div>
        </div>
        <nav className="p-3 space-y-1">
          <NavLink
            to="/admin"
            end
            className={({ isActive }) =>
              `flex items-center gap-2 px-3 py-2 rounded ${isActive ? "bg-[#eaf3fb] text-brand" : "hover:bg-gray-100"}`
            }
          >
            <LayoutDashboard className="w-4 h-4" />
            Dashboard
          </NavLink>
          <NavLink
            to="/admin/media"
            className={({ isActive }) =>
              `block px-3 py-2 rounded ${isActive ? "bg-[#eaf3fb] text-[#1F75BB]" : "hover:bg-gray-100"}`
            }
          >
            Mdias
          </NavLink>
          {/* Exemple de lien rserv superAdmin */}
          {role === "superAdmin" && (
            <NavLink
              to="/admin/gestion-admins"
              className={({ isActive }) =>
                `block px-3 py-2 rounded ${isActive ? "bg-[#eaf3fb] text-[#1F75BB]" : "hover:bg-gray-100"}`
              }
            >
              Gestion des admins
            </NavLink>
          )}
        </nav>

        <div className="p-3 mt-auto">
          <button
            onClick={logout}
            className="w-full flex items-center justify-center gap-2 bg-red-50 text-red-600 px-3 py-2 rounded hover:bg-red-100"
          >
            <LogOut className="w-4 h-4" />
            Dconnexion
          </button>
        </div>
      </aside>

      {/* Content */}
      <main className="col-span-12 md:col-span-9 lg:col-span-10 p-4">
        <Outlet />
      </main>
    </div>
  );
}
